.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/*Whole body*/
*{
  color:#653780;
  font-family:  Bradley Hand ,cursive;
  font-size: 1vw;
}
.card{
  color:#653780;
  font-family:  Bradley Hand ,cursive;
  font-size: 1em;
  margin-top: 10px;
}
/*Skills icon*/
.flex-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 100px;
  padding: 15px;
 
 
  margin-top: 50px;
}

.social{
  justify-content: center;
  gap:50px;
  
}

